<template>
  <div class="school-detail">
    <table class="ui very basic celled table" v-if="schools.length">
      <thead>
        <tr>
          <th>ID / 名称</th>
          <th>管理员</th>
          <th>学制</th>
          <th>老师数目</th>
          <th>学生数目</th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="school of schools">
          <td> {{school.id}} / {{school.name}} </td>
          <td>
            <div class="display-flex" v-for="administrator of school.administrators">
              <label>{{administrator.username}}</label>
              <span>{{administrator.phone}}</span>
            </div>
          </td>
          <td> {{school.programLengthLabel}} </td>
          <td> {{school.nStudent}} </td>
          <td> {{school.nTeacher}} </td>
        </tr>
      </tbody>
    </table>
  </div>
</template>

<script>
export default {
  name: 'school-detail',
  props: {
    schools: { type: Array, required: true },
  },
  data() {
    return {}
  },
}
</script>

<style lang="less" scoped>
.ui.table {
  margin: .5rem 0;
  padding: 1rem;
  background-color: #f8f8f8;
  thead th, tbody tr {
    color: #666;
  }
}

.display-flex {
  display: flex;
  label {
    width: 6rem;
    font-weight: bold;
  }
  .flex-1 {
    flex: 1;
  }
}
</style>



