<template>
  <div class="ui vertical inverted menu" v-if="teacher">
    <div class="brand item">
      <img class="ui tiny image" src="~assets/img/brand-logo.png">
    </div>
    <router-link :to="{name: 'admin-search'}" tag="a" class="item">
      <i class="ui search icon"></i>搜索
    </router-link>
    <router-link :to="{name: 'admin-school'}" tag="a" class="item">
      <icon name="university"></icon>学校管理
    </router-link>
    <a class="item" href="/pad/logout">
      <icon name="sign-out"></icon> {{teacher.username}} (登出)
    </a>
  </div>
</template>

<script>
import { mapState } from 'vuex'
import axios from 'axios'
import 'components/icons/university'
import 'components/icons/sign-out'
import Icon from 'components/icons/Icon'

export default {
  name: 'vertical-navbar',
  data() {
    return {}
  },
  props: {
    activeTab: { type: String },
  },
  components: { Icon },
  created() {
    axios.get('/pad/j/teacher_info').then((response) => {
      const teacher = response.data.teacher
      this.$store.commit('updateTeacher', { teacher })
      if (process.env.NODE_ENV === 'production') {
        this.$raven.setUserContext({
          teacherName: teacher.username,
          teacherId: teacher.id,
        })
      }
    })
  },
  computed: {
    ...mapState(['teacher']),
  },
}
</script>

<style lang="less" scoped>
.ui.vertical.menu {
  border-radius: 0;
  width: 13rem;
  top: 0;
  margin: 0;
  border: none;
  position: fixed;
  height: 100vh;
  box-shadow: 0 1px 5px rgba(0,0,0,0.15);
  .brand.item {
    background: #373d41;
    padding-top: 2em;
    padding-bottom: 2em;
  }
}

.ui.vertical.menu .item>i.icon {
  float: none;
  margin: 0 .5em 0 0;
}

.ui.inverted.menu {
  background: #42485B;
  font-size: 12px;
}

</style>



