<template>
  <div class="school-manage">
    <template v-if="school && classes">
      <h1 class="ui header">{{school.name}}</h1>
      <p><a class="ui small blue label">{{school.programLengthLabel}}</a> 老师 {{school.nTeacher}} 人，学生 {{school.nStudent}} 人，共 {{classes.length}} 个行政班。</p>
    </template>
    <div class="ui secondary pointing menu">
      <a
        class="item"
        @click="tabName='classes'"
        :class="tabName==='classes'?'active':''">
        行政班
      </a>
    </div>
    <div v-if="tabName==='classes' && school" class="classes-panel">
      <div class="left-list-section">
        <table class="ui padded small table">
          <thead>
            <tr>
              <th>ID</th>
              <th>行政班名</th>
              <th>年级</th>
              <th>学生数</th>
              <th>班主任</th>
              <th>创建年份</th>
              <th>状态</th>
              <th>操作</th>
            </tr>
          </thead>
          <tbody>
            <tr v-for="c of classes" :key="c.id" @click="getClassDetail(c.id)">
              <td>{{c.id}}</td>
              <td>{{c.name}}</td>
              <td>{{getGradeLabel(c.gradeId)}}</td>
              <td>{{c.nStudent}}</td>
              <td>{{c.teacher.name}}</td>
              <td>{{c.year}}</td>
              <td>{{getClassSatusLabel(c)}}</td>
              <td>
                <!-- <span -->
                <!--   v-if="canBeUpdated(c)" -->
                <!--   @click.stop="checkForUpgrade(c)" -->
                <!--   class="link link-primary">升年级开新行政班</span><br> -->
                <!-- <span -->
                <!--   v-if="canBeArchived(c)" -->
                <!--   class="link link-primary">归档</span> -->
              </td>
            </tr>
          </tbody>
        </table>
      </div>
      <div class="right-detail-section">
        <div class="class-teachers" v-if="curClass">
          <h3 class="ui header">
            <span class="id-label">{{curClass.id}}</span> {{curClass.name}}
          </h3>
          <span class="text-muted">班主任：{{curClass.teacher.name}}[{{curClass.teacher.id}}]</span>
        </div>
        <div class="class-courses" v-if="curClass">
          <h4 class="ui header">关联教学班（{{curClass.courses.length}}）</h4>
          <table class="ui padded small table">
            <thead>
              <tr>
                <th>ID</th>
                <th>名称</th>
                <th>学科</th>
                <th>年级</th>
                <th>任课老师</th>
                <th>状态</th>
              </tr>
            </thead>
            <tbody>
              <tr :key="c.id" v-for="c of curClass.courses">
                <td>{{c.id}}</td>
                <td>{{c.name}}</td>
                <td>{{subjects[c.subjectId]}}</td>
                <td>{{grades[c.gradeId]}}</td>
                <td>{{c.teacher.name}}</td>
                <td>{{getCourseSatusLabel(c)}}</td>
              </tr>
            </tbody>
          </table>
        </div>

        <div class="class-teachers" v-if="curClass">
          <h4 class="ui header">
            所有学生（{{curClass.students.length}}人）
            <button
              class="link link-default"
              v-if="curClass.status===CLASS_STATUS_NORMAL"
              @click="openAddStudent">+ 增加学生</button>
          </h4>
          <span
            v-for="s of curClass.students"
            class="ui label s-label"
            @click="fetchStudent(s.id)"
            >{{s.name}}[{{s.id}}]</span>
        </div>
      </div>
    </div>
    <modal
      @close="closeUpgradeModal"
      key="fork-and-upgrade-class"
      v-if="classToUpgrade"
      :with-header="false"
      class="modal-default">
      <div slot="body">
        <p class="text-muted">会生成新的行政班，年级增加1，并且会生成相应的教学班级。<br>操作不可逆，且只能执行一次。</p>
        <table class="ui small padded  table">
          <thead>
            <tr>
              <th>原行政班</th>
              <th>升年级新的行政班</th>
            </tr>
          </thead>
          <tbody>
            <tr>
              <td>{{classToUpgrade.name}}</td>
              <td>
                <div class="ui input">
                  <input type="text" id="c-title" v-model="newName" placeholder="不超过60个字符">
                </div>
              </td>
            </tr>
            <tr>
              <td>{{grades[classToUpgrade.gradeId]}}</td>
              <td>{{grades[newGradeId]}}</td>
            </tr>
          </tbody>
        </table>
        <span class="text-muted">仔细对照基本信息, 确保新名字和年级正确。</span>
      </div>
      <div slot="footer">
        <div class="upgrade-actions">
          <button class="ui tiny blue button" @click="forkAndUpgrade(classToUpgrade, archive=false)">只升级</button>
          <button class="ui tiny blue button" @click="forkAndUpgrade(classToUpgrade, archive=true)">升级并归档(原行政班和教学班)</button>
        </div>
        <div class="upgrade-hint">
          请稍后...正在更新...
        </div>
      </div>
    </modal>
    <modal
      @close="studentToEdit=null"
      key="edit-student-modal"
      v-if="studentToEdit"
      :with-header="false"
      :with-footer="false"
      class="modal-default">
      <div slot="body">
        <h3 class="ui header"><span class="id-label">{{studentToEdit.id}}</span> {{studentToEdit.name}}</h3>
        <p class="text-muted">学生状态：{{studentToEdit.status === 0 ? '正常' : '已封禁'}}
          <button
            class="ui tiny red button"
            v-if="studentToEdit.status===0"
            @click="ban_student(studentToEdit)">封禁该学生</button>
        </p>
        <p class="text-muted">退出行政班：不保留历史记录</p>
        <table class="ui padded small table">
          <thead>
            <tr>
              <th>所在行政班</th>
              <th>操作</th>
              <th>状态</th>
            </tr>
          </thead>
          <tbody>
            <tr :key="'studentToEdit-' + c.id" v-for="c of studentToEdit.classes">
              <td>{{c.id}}{{c.name}}  <span v-if="c.id===curClass.id" class="text-muted">当前班级</span></td>
              <td>
                <button
                  class="ui tiny blue button"
                  v-if="c.status === CLASS_STATUS_NORMAL"
                  @click="student_exit_class(studentToEdit, c)">退班</button>
              </td>
              <td>{{getClassSatusLabel(c)}}</td>
            </tr>
          </tbody>
        </table>
      </div>
    </modal>
    <modal
      @close="openAddStudentModal=false"
      key="add-student-modal"
      v-if="openAddStudentModal"
      :with-header="false"
      :with-footer="false"
      class="modal-default">
      <div slot="body">
        <h3 class="ui header">增加学生</h3>
        <div class="ui action input">
          <input type="number" v-model="idToAddStudent" placeholder="输入学生 ID">
          <button
            @click="addStudentToClass('joinById', curClass)"
            class="ui button" >根据 ID 加入行政班</button>
        </div>
        <div class="ui action input">
          <input type="text" v-model="nameToCreateStudent" placeholder="输入学生名字">
          <button
            @click="addStudentToClass('createAndJoin', curClass)"
            class="ui button">创建账号并加入行政班</button>
        </div>
      </div>
    </modal>
  </div>
</template>

<script>

import Modal from 'components/modal'
import axios from 'axios'
import qs from 'qs'
import {
  CLASS_STATUS_MAP,
  COURSE_STATUS_MAP,
  CLASS_STATUS_ARCHIVED,
  CLASS_STATUS_NORMAL,
  GRADES,
  SUBJECTS,
  COURSE_STATUS_NORMAL,
} from 'assets/js/consts/common'

export default {
  name: 'school-detail',
  data() {
    return {
      tabName: 'classes',
      schoolId: this.$route.params.schoolId,
      courses: [],
      classes: [],
      school: null,
      classToUpgrade: null,
      grades: GRADES,
      subjects: SUBJECTS,
      newGradeId: null,
      newName: '',
      curClass: null,
      studentToEdit: null,
      CLASS_STATUS_NORMAL,
      idToAddStudent: 0,
      nameToCreateStudent: '',
      openAddStudentModal: false
    }
  },
  components: { Modal },
  created() {
    if (this.tabName === 'classes') this.getClasses()
    else if (this.tabName === 'courses') this.getCourses()

    axios.get(`/pad/j/admin/school/${this.schoolId}`).then((resp) => {
      if (resp.data.status === 1) {
        this.$toasted.error(resp.data.detail, { duration: 800, postion: 'bottom-center' })
      } else if (resp.data.status === 0) {
        this.school = resp.data.school
      }
    })
  },
  watch: {
    tabName: function(val, oldVal) {
      if (val === 'classes') this.getClasses()
      else if (val === 'courses') this.getCourses()
    },
  },
  methods: {
    openAddStudent() {
      this.openAddStudentModal = true
      this.nameToCreateStudent = ''
      this.idToAddStudent = 0
    },
    closeUpgradeModal() {
      this.classToUpgrade = null
      this.newGradeId = null
      this.newName = ''
    },
    gradeStepOne(c) {
      if (!this.school) {
        this.$toasted.error('正在获取学校数据，稍后...', { duration: 800, postion: 'bottom-center' })
        return
      }
      switch (c.gradeId) {
        case 9: return 8;
        case 8: return 7;
        case 7: return 20;
        case 5: return 4;
        case 4: return 3;
        default: return null
      }
    },
    predictClassName(c) {
      const replaceMap = [
        ['初一', '初二'],
        ['初二', '初三'],
        ['初三', '初四'],
        ['高一', '高二'],
        ['高二', '高三'],
      ]
      for (const item of replaceMap) {
        const [toBeReplaced, newStr] = item
        if (c.name.includes(toBeReplaced)) {
          return c.name.replace(toBeReplaced, newStr)
        }
      }
      return ''
    },
    checkForUpgrade(c) {
      if (!this.school) {
        this.$toasted.error('正在获取学校数据，稍后...', { duration: 800, postion: 'bottom-center' })
        return
      }
      if (c.upToClassId) {
        this.$toasted.error('该行政班已升过年级。', { duration: 800, postion: 'bottom-center' })
      } else {
        this.classToUpgrade = c
        this.newGradeId = this.gradeStepOne(c)
        this.newName = this.predictClassName(c)
      }
    },
    canBeUpdated(c) {
      if (c.upToClassId) return false
      if (![9, 8, 7, 5, 4].includes(c.gradeId)) return false
      if (this.school.programLength === 1 && c.gradeId === 7) return false
      if (c.year === new Date().getFullYear()) return false
      return true
    },
    upgradeActionToggle(on = true) {
      if (on) {
        document.querySelector('.upgrade-hint').style.display = 'none'
        document.querySelector('.upgrade-actions').style.display = 'block'
      } else {
        document.querySelector('.upgrade-actions').style.display = 'none'
        document.querySelector('.upgrade-hint').style.display = 'block'
      }
    },
    forkAndUpgrade(c, archive = false) {
      if (!this.newName || !this.newGradeId) {
        this.$toasted.error('新行政班名字和年级不能为空', { duration: 800, postion: 'bottom-center' })
      }
      const params = {
        newGradeId: this.newGradeId,
        newName: this.newName,
        archive,
      }
      this.upgradeActionToggle(false)
      axios.post(`/pad/j/admin/class/${c.id}/forkandupgrade/`, qs.stringify(params)).then((resp) => {
        if (resp.data.status === 1) {
          this.$toasted.error(resp.data.detail, { duration: 800, postion: 'bottom-center' })
        } else if (resp.data.status === 0) {
          this.classes.push(resp.data.newClass)
          c.status = CLASS_STATUS_ARCHIVED
          c.upToClassId = resp.data.newClass.id
          this.closeUpgradeModal()
        }
        this.upgradeActionToggle(true)
      })
    },
    fetchStudent(sid) {
      axios.get(`/pad/j/admin/student/${sid}/class_info`).then((resp) => {
        if (resp.data.status === 1) {
          this.$toasted.error(resp.data.detail, { duration: 800, postion: 'bottom-center' })
        } else if (resp.data.status === 0) {
          this.studentToEdit = resp.data.student
        }
      })
    },
    ban_student(student) {
      axios.post(`/pad/j/admin/student/${student.id}/ban/`).then((resp) => {
        if (resp.data.status === 1) {
          this.$toasted.error(resp.data.detail, { duration: 800, postion: 'bottom-center' })
        } else if (resp.data.status === 0) {
          this.$toasted.success('封禁成功，请关闭弹窗重新查看数据', { duration: 800, postion: 'bottom-center' })
        }
      })
    },
    addStudentToClass(opName, klass) {
      const params = {
        opName,
        studentId: this.idToAddStudent,
        studentName: this.nameToCreateStudent,
      }
      axios.post(`/pad/j/admin/class/${klass.id}/add_student/`, qs.stringify(params)).then((resp) => {
        if (resp.data.status === 1) {
          this.$toasted.error(resp.data.detail, { duration: 800, postion: 'bottom-center' })
        } else if (resp.data.status === 0) {
          this.$toasted.success('增加成功，请重新查看数据', { duration: 800, postion: 'bottom-center' })
        }
        this.openAddStudentModal = false
      })
    },
    student_exit_class(student, klass) {
      const params = {
        studentId: student.id,
        classId: klass.id,
      }
      axios.post('/pad/j/admin/student/exit_class/', qs.stringify(params)).then((resp) => {
        if (resp.data.status === 1) {
          this.$toasted.error(resp.data.detail, { duration: 800, postion: 'bottom-center' })
        } else if (resp.data.status === 0) {
          this.$toasted.success('退班成功，请关闭弹窗重新查看数据', { duration: 800, postion: 'bottom-center' })
        }
      })
    },
    getClasses() {
      axios.get(`/pad/j/admin/school/${this.schoolId}/classes`).then((resp) => {
        if (resp.data.status === 1) {
          this.$toasted.error(resp.data.detail, { duration: 800, postion: 'bottom-center' })
        } else if (resp.data.status === 0) {
          this.classes = resp.data.classes
        }
      })
    },
    getClassDetail(classId) {
      axios.get(`/pad/j/admin/class/${classId}`).then((resp) => {
        if (resp.data.status === 1) {
          this.$toasted.error(resp.data.detail, { duration: 800, postion: 'bottom-center' })
        } else if (resp.data.status === 0) {
          this.curClass = resp.data.classDetail
        }
      })
    },
    getCourses() {
      axios.get(`/pad/j/admin/school/${this.schoolId}/courses`).then((resp) => {
        if (resp.data.status === 1) {
          this.$toasted.error(resp.data.detail, { duration: 800, postion: 'bottom-center' })
        } else if (resp.data.status === 0) {
          this.courses = resp.data.courses
        }
      })
    },
    getClassSatusLabel(c) {
      return c && CLASS_STATUS_MAP[c.status]
    },
    getCourseSatusLabel(course) {
      return course && COURSE_STATUS_MAP[course.status]
    },
    getGradeLabel(gradeId) {
      return gradeId && GRADES[gradeId]
    },
    canBeArchived(c) {
      return c.status === COURSE_STATUS_NORMAL
    },
  },
}
</script>

<style lang="less" scoped>
.upgrade-hint {
  display: none;
  color: red;
  font-size: .9em;
}
.modal-default .ui.padded.table {
  width: 100%;
}
.modal-default {
  .text-muted {
    font-size: .9em;
  }
}
.school-manage {
  padding: 1rem;
}
.classes-panel {
  display: flex;
  .ui.padded.table {
    border-radius: 0;
    border-left: none;
    border-right: none;
    td, th {
      padding: .5em 1em;
    }
    tbody tr {
      cursor: pointer;
      &:hover, &:focus, &:active, &.active {
        background-color: #f9fafb;
      }
    }
  }
  .left-list-section {
    flex: 3;
    max-height: 80vh;
    overflow-y: auto;
  }
  .right-detail-section {
    flex: 2;
    padding: 0 1em;
    .class-teachers, .class-courses {
      margin-bottom: 2rem;
      .ui.header {
        margin-bottom: 6px;
      }
    }
  }
}
.classes-panel {
  position: relative;
  table {
  }
  .class-detail {
    background-color: #eee;
    width: 350px;
    position: fixed;
    top: 5rem;
    right: 1rem;
    bottom: 0;
    overflow-y: auto;
  }
}
.id-label {
  display: inline-block;
  padding: .3em;
  background-color: #f0f0f0;
}
.ui.label {
  background-color: #f6f6f6;
  margin-bottom: .5em;
}
.ui.label.s-label {
  min-width: 7em;
  font-size: .8rem;
}
.ui.input.action {
  margin-bottom: .5rem;
}
</style>



