import Vue from 'vue'
import Vuex from 'vuex'
import Raven from 'raven-js'
import RavenVue from 'raven-js/plugins/vue'
import Toasted from 'vue-toasted'
import VueRouter from 'vue-router'
import VerticalNavbar from './vertical-navbar'
import SchoolList from './school-list'
import SchoolDetail from './school-detail'
import Search from './search/index'

require('./index.less')

Vue.config.productionTip = false

var axios = require('axios')

axios.defaults.headers.common['X_REQUESTED_WITH'] = 'XMLHttpRequest'
axios.defaults.xsrfHeaderName = 'X-CSRFTOKEN'
axios.defaults.xsrfCookieName = 'csrftoken'
Vue.prototype.$raven = Raven
if (process.env.NODE_ENV === 'production') {
  Raven.config('http://216e3d581cd9424fad7f5d8c046b2f64@sentry.xdf100.com/6').addPlugin(RavenVue, Vue).install()
}


Vue.use(Vuex)
Vue.use(VueRouter)
Vue.use(Toasted)


const router = new VueRouter({
  mode: 'history',
  base: process.env.NODE_ENV === 'production' ? '/pad/admin' : '/static/w/pages/admin.html',
  routes: [
    {
      path: '/', redirect: 'search',
    },
    {
      path: '/search', component: Search, name: 'admin-search',
    },
    {
      path: '/school', component: SchoolList, name: 'admin-school',
    },
    {
      path: '/school/:schoolId', component: SchoolDetail, name: 'admin-school-detail',
    },
  ],
  linkActiveClass: 'active',
})


var store = new Vuex.Store({
  state: {
    teacher: null,
  },
  mutations: {
    updateTeacher(state, payload) {
      state.teacher = payload.teacher
    },
  },
  getters: {
    school: state => state.teacher ? state.teacher.school : null,
  },
})


new Vue({
  delimiters: ['${', '}'],
  el: '#app',
  store,
  router,
  data: {},
  components: {
    VerticalNavbar,
  },
})



