<template>
  <div class="search-panel">
    <p class="note">一般来说，文本相关的搜索为模糊匹配；最多返回10条满足条件的信息；所有查询为单条件查询，不会组合查询。</p>
    <div class="search-block">
      <h2>搜索学校</h2>
      <div class="ui icon input">
        <input type="text" placeholder="输入学校名称..." v-model="schoolName" @keyup.enter="searchSchool()">
        <i class="search link icon" @click="searchSchool()"></i>
      </div>
      <school-detail :schools="schools" v-if="schools.length"></school-detail>
    </div>
    <div class="search-block">
      <h2>搜索学生</h2>
      <div class="ui icon input">
        <input type="text" placeholder="输入学生姓名..." v-model="studentName"  @keyup.enter="searchStudent('name')">
        <i class="search link icon" @click="searchStudent('name')"></i>
      </div>
      <div class="ui icon input">
        <input type="number" placeholder="输入学生ID..." v-model="studentId"  @keyup.enter="searchStudent('id')">
        <i class="search link icon" @click="searchStudent('id')"></i>
      </div>
      <student-detail :students="students" v-if="students.length"></student-detail>
    </div>
    <div class="search-block">
      <h2>搜索老师</h2>
      <div class="ui icon input">
        <input type="text" placeholder="输入老师姓名..." v-model="teacherName" @keyup.enter="searchTeacher('name')">
        <i class="search link icon" @click="searchTeacher('name')"></i>
      </div>
      <div class="ui icon input">
        <input type="number" placeholder="输入老师手机号..." v-model="teacherPhone" @keyup.enter="searchTeacher('phone')">
        <i class="search link icon" @click="searchTeacher('phone')"></i>
      </div>
      <div class="ui icon input">
        <input type="number" placeholder="输入老师ID..." v-model="teacherId" @keyup.enter="searchTeacher('id')">
        <i class="search link icon" @click="searchTeacher('id')"></i>
      </div>
      <teacher-detail :teachers="teachers"
        v-if="teachers.length"></teacher-detail>
    </div>
    <div class="search-block">
      <h2>搜索文件</h2>
      <div class="ui icon input">
        <input type="text" placeholder="输入文件名称..." v-model="documentName" @keyup.enter="searchDocument()">
        <i class="search link icon" @click="searchDocument()"></i>
      </div>
      <document-detail :documents="documents"
        v-if="documents.length"></document-detail>
    </div>
  </div>
</template>

<script>
import axios from 'axios'
import qs from 'qs'
import StudentDetail from './student-detail'
import TeacherDetail from './teacher-detail'
import SchoolDetail from './school-detail'
import DocumentDetail from './document-detail'

export default {
  name: 'search-panel',
  data() {
    return {
      students: [],
      studentName: '',
      studentId: null,
      schools: [],
      schoolName: '',
      teachers: [],
      teacherName: '',
      teacherPhone: null,
      teacherId: null,
      documents: [],
      documentName: '',
    }
  },
  components: { StudentDetail, TeacherDetail, SchoolDetail, DocumentDetail },
  created() {
  },
  methods: {
    searchStudent(type_ = 'name') {
      const params = { searchType: type_ }
      if (type_ === 'name') {
        if (!this.studentName) {
          this.$toasted.error('姓名不能为空', { duration: 800, position: 'bottom-center' })
          return
        }
        params.searchName = this.studentName
      } else if (type_ === 'id') {
        if (!this.studentId) {
          this.$toasted.error('请输入ID', { duration: 800, position: 'bottom-center' })
          return
        }
        params.searchId = this.studentId
      }
      axios.post('/pad/j/admin/search/student', qs.stringify(params)).then((response) => {
        if (response.data.status === 0) {
          this.students = response.data.students
          if (response.data.students.length === 0) {
            this.$toasted.info('没有搜索到匹配的条目', { duration: 800, position: 'bottom-center' })
          }
        } else if (response.data.status === 1) {
          this.$toasted.error(response.data.detail, { duration: 800, position: 'bottom-center' })
        }
      })
    },
    searchSchool() {
      if (!this.schoolName) {
        this.$toasted.error('学校名称不能为空', { duration: 800, position: 'bottom-center' })
        return
      }
      axios.post('/pad/j/admin/search/school', qs.stringify({ searchName: this.schoolName })).then((response) => {
        if (response.data.status === 0) {
          this.schools = response.data.schools
          if (response.data.schools.length === 0) {
            this.$toasted.info('没有搜索到匹配的条目', { duration: 800, position: 'bottom-center' })
          }
        } else if (response.data.status === 1) {
          this.$toasted.error(response.data.detail, { duration: 800, position: 'bottom-center' })
        }
      })
    },
    searchTeacher(type_ = 'name') {
      const params = { searchType: type_ }
      if (type_ === 'name') {
        if (!this.teacherName) {
          this.$toasted.error('姓名不能为空', { duration: 800, position: 'bottom-center' })
          return
        }
        params.searchName = this.teacherName
      } else if (type_ === 'id') {
        if (!this.teacherId) {
          this.$toasted.error('请输入ID', { duration: 800, position: 'bottom-center' })
          return
        }
        params.searchId = this.teacherId
      } else if (type_ === 'phone') {
        if (!this.teacherPhone) {
          this.$toasted.error('请输入手机号', { duration: 800, position: 'bottom-center' })
          return
        }
        params.searchPhone = this.teacherPhone
      }
      axios.post('/pad/j/admin/search/teacher', qs.stringify(params)).then((response) => {
        if (response.data.status === 0) {
          this.teachers = response.data.teachers
          if (response.data.teachers.length === 0) {
            this.$toasted.info('没有搜索到匹配的条目', { duration: 800, position: 'bottom-center' })
          }
        } else if (response.data.status === 1) {
          this.$toasted.error(response.data.detail, { duration: 800, position: 'bottom-center' })
        }
      })
    },
    searchDocument() {
      if (!this.documentName) {
        this.$toasted.error('文档名不能为空', { duration: 800, position: 'bottom-center' })
        return
      }
      axios.post('/pad/j/admin/search/document', qs.stringify({ searchName: this.documentName })).then((response) => {
        if (response.data.status === 0) {
          this.documents = response.data.documents
          if (response.data.documents.length === 0) {
            this.$toasted.info('没有搜索到匹配的条目', { duration: 800, position: 'bottom-center' })
          }
        } else if (response.data.status === 1) {
          this.$toasted.error(response.data.detail, { duration: 800, position: 'bottom-center' })
        }
      })
    },
  },
}
</script>

<style lang="less" scoped>
.search-panel {
  font-size: 12px;
  padding: 1rem 2rem;
}
.link:focus, .link:hover, .link:active {
  color: inherit;
}
</style>



