<template>
  <div class="school-manage">
    <table class="ui small table">
      <thead>
        <tr>
          <th>ID</th>
          <th>学校名称</th>
          <th>老师数目</th>
          <th>学生数目</th>
          <th>学制</th>
          <th>暑期视频</th>
          <th>头图</th>
        </tr>
      </thead>
      <tbody>
        <tr
          v-for="s of schools"
          :key="s.id"
          class="school-row"
          @click="$router.push({name: 'admin-school-detail', params: {schoolId: s.id}})">
          <td class="col-2w">{{s.id}}</td>
          <td class="school-name">{{s.name}}</td>
          <td class="col-6w">{{s.nTeacher}}</td>
          <td class="col-6w">{{s.nStudent}}</td>
          <td>{{s.programLengthLabel}}</td>
          <td class="col-6w">{{s.campLessonCount}}</td>
          <td>
            <img :src="s.posterUrl" alt="头图" v-if="s.posterUrl">
            <label :for="'poster'+s.id" @click.stop="">
              <input type="file" :id="'poster'+s.id" class="hidden" @change="uploadPoster(s, $event)" accept="image/*">
              <span class="link link-primary">{{s.posterUrl?'修改':'上传'}}</span>
            </label>
          </td>
        </tr>
      </tbody>
      <tfoot>
        <tr>
          <th></th>
          <th>共 {{schools.length}} 个学校</th>
          <th></th>
          <th></th>
          <th></th>
          <th></th>
          <th></th>
        </tr>
      </tfoot>
    </table>
  </div>
</template>

<script>
import axios from 'axios'
import qs from 'qs'

const region = 'oss-cn-beijing'
const bucket = process.env.NODE_ENV === 'production' ? 'padmedia' : 'padmedia-dev'

export default {
  name: 'school-list',
  data() {
    return {
      schools: [],
      ossBucket: null,
    }
  },
  created() {
    axios.get('/pad/j/manage/school/list').then((response) => {
      this.schools = response.data.schools
    }).then(() => {
      axios.get('/pad/j/misc/aliyun/sts').then((response) => {
        this.ossBucket = new OSS.Wrapper({
          region,
          bucket,
          ...response.data.sts,
        })
      })
    })
  },
  methods: {
    uploadPoster(school, event) {
      const file = event.target.files[0]
      const extension = file.name.split('.').pop()
      if (['jpg', 'bmp', 'png', 'jpeg'].indexOf(extension.toLowerCase()) < 0) {
        this.$toasted.error('请上传 jpg、bmp、png、jpeg 格式的图片', {
          duration: 1000,
          position: 'top-center',
        })
        return
      }
      const filename = Math.random().toString(36).substr(2, 15)
      const key = `school/poster/${filename}.${extension}`
      const params = { schoolId: school.id, posterKey: key }
      const $link = $(event.target).next('.link')
      $link.addClass('loading')
      this.ossBucket.multipartUpload(key, file).then(() => {
        axios.post('/pad/j/manage/school/upload/poster', qs.stringify(params)).then((response) => {
          $link.removeClass('loading')
          const index = this.schools.indexOf(school)
          this.schools.splice(index, 1, response.data.school)
        })
      })
    },
  },
}
</script>

<style lang="less" scoped>
  .school-manage {
    padding: 1rem;
  }
  td img {
    max-width: 4rem;
    max-height: 4rem;
  }
  .ui.small.table .school-row {
    cursor: pointer;
    &:hover, &:focus, &:active {
      background-color: #f9fafb;
    }
    .school-name {
      font-weight: bold;
    }
    .col-2w {
      width: 2rem;
    }
    .col-6w {
      width: 6rem;
    }
  }
</style>



