<template>
  <div class="teacher-detail">
    <table class="ui very basic celled table" v-if="teachers.length">
      <thead>
        <tr>
          <th>ID / 名字</th>
          <th>手机号</th>
          <th>学校ID / 名称</th>
          <th>其他</th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="teacher of teachers">
          <td> {{teacher.id}} / {{teacher.username}} </td>
          <td> {{teacher.phone}} </td>
          <td> {{teacher.school.id}} / {{teacher.school.name}} </td>
          <td>
            <div class="display-flex">
              <label> accesstoken </label>
              <span class="flex-1">{{teacher.accessToken ? teacher.accessToken : '无'}}</span>
            </div>
          </td>
        </tr>
      </tbody>
    </table>
  </div>
</template>

<script>
export default {
  name: 'teacher-detail',
  props: {
    teachers: { type: Array, required: true },
  },
  data() {
    return {}
  },
}
</script>

<style lang="less" scoped>
.ui.table {
  margin: .5rem 0;
  padding: 1rem;
  background-color: #f8f8f8;
  thead th, tbody tr {
    color: #666;
  }
}
.display-flex {
  display: flex;
  label {
    width: 6rem;
    font-weight: bold;
  }
  .flex-1 {
    flex: 1;
  }
}
</style>



