<template>
  <div class="document-detail">
    <table class="ui very basic celled table" v-if="documents.length">
      <thead>
        <tr>
          <th>ID / 名字</th>
          <th>类型 / 大小</th>
          <th>年级</th>
          <th>其他</th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="document of documents">
          <td> {{document.id}} / {{document.name}} </td>
          <td> {{fileTypeNameMap[document.fileType]}} / {{sizeDisplay(document.fileSize)}} </td>
          <td> {{grades[document.gradeId]}} </td>
          <td>
            <div class="display-flex" v-if="document.padSchoolId===0">
              <label>平台资源</label>
            </div>
            <div class="display-flex" v-else>
              <label>老师</label>
              <span class="flex-1">{{document.teacher && document.teacher.username}}</span>
            </div>
            <div class="display-flex">
              <label>url</label>
              <span class="flex-1">{{document.fileUrl}}</span>
            </div>
          </td>
        </tr>
      </tbody>
    </table>
  </div>
</template>

<script>
import { RESOURCE_TYPE_NAME_MAP, GRADES } from 'assets/js/consts/common'

export default {
  name: 'document-detail',
  props: {
    documents: { type: Array, required: true },
  },
  data() {
    return {
      fileTypeNameMap: RESOURCE_TYPE_NAME_MAP,
      grades: GRADES,
    }
  },
  methods: {
    sizeDisplay(size) {
      if (size > 1000000) return `${(size / 1000000).toFixed(1)}MB`
      if (size > 1000) return `${(size / 1000).toFixed(1)}KB`
      return `${size}B`
    },
  },
}
</script>

<style lang="less" scoped>
.ui.table {
  margin: .5rem 0;
  padding: 1rem;
  background-color: #f8f8f8;
  thead th, tbody tr {
    color: #666;
  }
}
.display-flex {
  display: flex;
  max-width: 35rem;
  margin-bottom: 5px;
  label {
    width: 4rem;
    font-weight: bold;
  }
  .flex-1 {
    flex: 1;
    word-break: break-all;
  }
}
</style>



