<template>
 <transition name="modal">
    <div class="modal-mask" @click.self="$emit('close')">
      <div class="modal-wrapper">
        <div class="modal-container">
          <div class="modal-header" v-if="withHeader">
            <slot name="header"></slot>
            <button class="ui tiny icon button" @click="$emit('close')">
              <icon name="slim-remove"></icon>
            </button>
          </div>
          <div class="modal-body">
            <slot name="body"></slot>
          </div>
          <div class="modal-footer" v-if="withFooter">
            <slot name="footer"></slot>
          </div>
        </div>
      </div>
    </div>
  </transition>
</template>

<script>
/*
样式(class)选择：modal-large, modal-massive, modal-small, modal-tiny, modal-default
尽量不要嵌套
*/

import 'components/icons/slim-remove'
import Icon from 'components/icons/Icon'

export default {
  name: 'modal',
  props: {
    withHeader: { required: false, default: true },
    withFooter: { required: false, default: true },
    escBinding: { required: false, default: true },
  },
  data() {
    return {
      scrollbarWidth: window.innerWidth - document.body.clientWidth,
      originalBodyPad: document.body.style.paddingRight || '',
      bodyPad: parseInt((document.body.style.paddingRight || 0), 10),
    }
  },
  components: { Icon },
  mounted() {
    document.body.classList.add('modal-open')
    document.body.style.paddingRight = `${this.scrollbarWidth + this.bodyPad}px`
    if (this.escBinding) window.addEventListener('keyup', this.closeSelf)
  },
  beforeDestroy() {
    if (this.escBinding) window.removeEventListener('keyup', this.closeSelf)
  },
  destroyed() {
    document.body.classList.remove('modal-open')
    document.body.style.paddingRight = this.originalBodyPad
  },
  methods: {
    closeSelf(e) {
      if (e.keyCode === 27) {
        this.$emit('close')
      }
    },
  },
}
</script>

<style lang="less">
@import '~assets/css/base-variables.less';

// modal-default 白色header
.modal-default .modal-header {
  background-color: white;
  color: @color-grey-800;
  border-bottom: solid 1px @color-grey-200;
  .ui.tiny.icon.button {
    color: @color-grey;
    &:hover, &:focus, &:active {
      color: @color-grey-800;
    }
  }
}
body.modal-open {
  overflow: hidden;
}
.modal-container {
  font-family: "Helvetica Neue", Helvetica, "PingFang SC", "Hiragino Sans GB", "Microsoft YaHei", "微软雅黑", Arial, sans-serif;
}
.modal-header {
  background-color: @color-blue;
  color: white;
  padding: 1em;
  display: flex;
  h3, h4 {
    flex: 1;
    margin: 0;
    font-weight: bold;
    span {
      font-weight: normal;
    }
  }
  h3 {
    font-size: 15px;
  }
  h4 {
    font-size: 14px;
  }
  .ui.tiny.icon.button {
    background-color: transparent !important;
    color: @color-grey-200;
    padding: 0 !important;
    &:hover, &:focus, &:active {
      color: white;
    }
    .fa-icon {
      margin-right: 0;
    }
  }
  .note {
    margin: 0;
    margin-top: .2em;
    color: @color-extra-light-gray;
    font-size: 12px;
    font-weight: normal;
  }
}


.modal-body .alert-text {
  font-size: 16px;
  font-weight: normal;
}
.modal-body .note {
  margin: 0;
  color: @font-color-base-light;
}

.modal-body .section.header {
  font-size: 15px;
  margin-top: 14px;
  margin-bottom: 7px;
  font-weight: normal;
}

.modal-body .section.header:first-child {
  margin-top: 0;
}
.modal-body {
  max-height: 80vh;
  overflow-y: auto;
  padding-top: 1.2em;
  padding-bottom: 1.2em;
}
.modal-body .ui.form {
  .field, .grouped.fields, .inline.field {
    margin: 0 0 1em 0;
  }
  .field>label, .grouped.fields>label, .inline.field>label {
    color: @color-grey-800;
  }
}
.modal-body .ui.mini.input input[type=text]{
  padding: .4em .8em;
  color: @color-grey-700;
  font-size: 13px;
  width: 30rem;
}
.modal-body .ui.checkbox label{
  color: @color-grey-800;
}
.modal-massive .modal-body {
  min-height: 50vh;
}
.modal-large .modal-body {
  min-height: 30vh;
}
.modal-small .modal-body {
  min-height: 30vh;
}
.modal-small .modal-body {
  min-height: 2rem;
}

.modal-footer {
  .ui.button, .ui.button {
    padding: .5em 1.2em;
    font-weight: normal;
  }
}
.model-head-bg-danger .modal-header{
  background-color: @color-red;
}
</style>

<style lang="less" scoped>
@import '~assets/css/base-variables.less';

.modal-mask {
  font-size: 14px;
  position: fixed;
  z-index: 9998;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, .5);
  transition: opacity .3s ease;

  display: flex;
  align-items: center;
  justify-content: flex-start;
  padding: 1rem;
  overflow-y: scroll;
  overflow-x: auto;
  flex-direction: column;
}

.modal-wrapper {
  display: table-cell;
  vertical-align: middle;
}

.modal-container {
  max-width: 90vw;
  width: 500px;
  margin: 0px auto;
  background-color: #fff;
  box-shadow: 0 2px 8px rgba(0, 0, 0, .33);
  transition: all .4s ease;
  border-radius: 8px;
}
.modal-header {
  border-radius: 6px 6px 0 0;
}
.modal-body {
  font-size: 14px;
}
.modal-header, .modal-body, .modal-footer {
  padding-right: 1em;
  padding-left: 1em;
}

@media (min-width: @max-width)
{
  .modal-massive .modal-container {
    width: @max-width;
  }
}

@media (max-width: @max-width)
{
  .modal-massive .modal-container {
    width: 95vw;
  }
}

.modal-large > .modal-wrapper >.modal-container {
  width: 720px;
}

.modal-small .modal-container {
  width: 550px;
}

.modal-tiny .modal-container {
  width: 350px;
}
.modal-footer {
  text-align: right;
  padding-top: .8em;
  padding-bottom: .8em;
}

.modal-enter {
  opacity: 0;
}

.modal-leave-active {
  opacity: 0;
}
</style>



