<template>
  <div class="student-detail">
    <table class="ui very basic celled table" v-if="students.length">
      <thead>
        <tr>
          <th>ID / 名字 / 最后登录 / accesstoken</th>
          <th>学校ID / 名称</th>
          <th>状态</th>
          <th>其他</th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="student of students">
          <td>
            <h4 class="ui image header">
              <img :src="student.avatarUrl" class="ui mini rounded image">
              <div class="content">
                {{student.id}} / {{student.username}}
                <div class="sub header">{{student.lastLogin}}</div>
                <div class="sub header">{{student.accessToken ? student.accessToken : '无'}}</div>
              </div>
            </h4>
          </td>
          <td>
            {{student.school.id}} / {{student.school.name}}
          </td>
          <td>
            {{student.status ? '已封禁' : '正常'}}
          </td>
          <td>
            <div>
              <span class="link link-primary" @click="resetPassword(student)">重置密码为123456</span>
            </div>
          </td>
        </tr>
      </tbody>
    </table>
  </div>
</template>

<script>
import axios from 'axios'
import qs from 'qs'

export default {
  name: 'student-detail',
  props: {
    students: { type: Array, required: true },
  },
  data() {
    return {}
  },
  methods: {
    resetPassword(student) {
      const params = {
        studentId: student.id,
        opName: 'resetPassword',
      }
      axios.post('/pad/j/admin/edit/student', qs.stringify(params)).then((response) => {
        if (response.data.status === 0) {
          this.$toasted.info(
            '重置成功，新密码为123456，登录后请及时修改密码',
            { duration: 2000, position: 'bottom-center' },
          )
        } else if (response.data.status === 1) {
          this.$toasted.error(
            response.data.detail,
            { duration: 800, position: 'bottom-center' },
          )
        }
      })
    },
  },
}
</script>

<style lang="less" scoped>
.ui.table {
  margin: .5rem 0;
  padding: 1rem;
  background-color: #f8f8f8;
  thead th, tbody tr {
    color: #666;
  }
}
.ui.header>.image:not(.icon), .ui.header>img {
  vertical-align: top;
}
.ui.header .content .sub.header {
  line-height: 1.3;
}
</style>



